body {
    font-family: 'Montserrat', sans-serif;
    overflow-x: hidden;
}

.images {
    -webkit-animation-name: dropFly;
            animation-name: dropFly;
	-webkit-animation-duration: 1.3s;
	        animation-duration: 1.3s;
    -webkit-animation-direction: alternate;
            animation-direction: alternate;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

li {
    display: inline-block;
    /*
    list-style-type: none;
    padding: 3px;
    */
}

a {
    text-decoration: none;
}

.mint {
    cursor: pointer;
}

.button-option {
    background-color: #0A0A0A;
    border: 2px solid white;
    color: white;
    border-radius: 5px;
    font-size: 0.7rem;
    text-decoration: none;
    font-weight: 600;
    width: 30px;
    height: 30px;
    text-align: center;
}

.minus-mint {
    padding-top: 10px;
}

.plus-mint {
    padding-top: 6.5px;
}

.button-option:hover {
    background-color: #AFAFAF;
    border: 2px solid #0A0A0A;
    color: black;
}

.container-full {
    width: 100vw;
    height: 100vh;
    background-color: #000000;
    background-size: cover;
    z-index: 1;
}

.lore-content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    background-color: #111111;
    background-image: url(/static/media/fondo.d34e036e.png);
    background-size: cover;
}

.lore-content-2 {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    background-color: #111111;
    background-image: url(/static/media/2hd.2030b559.png);
    background-size: cover;
}

.lore-content-text {
    align-self: center;
    /*background-image: url('../src/images/holograma.png');
    background-size: cover;*/
    margin: 25px;
    padding: 25px;
}

.content-color {
    background-color: #0A0A0A;
    border: 2px solid white;
    border-radius: 10px;
}

.lore-content-image {
    align-self: center;
}

.image-left {
    border: 2px solid white;
    border-radius: 10px;
    width: 90%;
    height: 90%;
    margin-left: 25px;
}

.image-right {
    border: 2px solid white;
    border-radius: 10px;
    width: 90%;
    height: 90%;
    margin-left: 20px;
}

.content-center {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 35px;
    margin-bottom: 35px;
}

.merch-content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    background-color: #111111;
    background-image: url(/static/media/fondo.d34e036e.png);
    background-size: cover;
}

.whitepaper-content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    background-color: #111111;
    background-image: url(/static/media/fondo.d34e036e.png);
    background-size: cover;
    height: 800px;
}

.button-whitepaper {
    background-color: #004A7F;
    -webkit-text-stroke: 2px black;
    color: white;
    border-radius: 5px;
    font-size: 1.2rem;
    padding: 1rem 2rem;
    text-decoration: none;
}

.button-container{
    display:inline-block;
    position:relative;
}

.button-container a{
    position: absolute;
    bottom: 2em;
    left: 40%;
}

.button-container h1{
    position: absolute;
    bottom: 3em;
    left: 30%;
}

.team-content {
    background-color: #111111;
    background-image: url(/static/media/fondo.d34e036e.png);
    background-size: cover;
    padding-top: 90px;
    height: 800px;
}

.team-content-bug {
    background-color: #111111;
    background-image: url(/static/media/fondo.d34e036e.png);
    background-size: cover;
    height: 750px;
}

.team-grid {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
}

.team-image {
    padding-left: 30px;
    padding-right: 30px;
    border-radius: 40px;
    border-inline: 10px;
}

.team-name {
    text-align: center;
    color: black;
    font-size: 1.2em;
}

.team-role {
    text-align: center;
    color: black;
    font-size: 1em;
    margin-bottom: 30px;
}

.faq-content {
    background-color: #111111;
    background-image: url(/static/media/faq1.94c4ed70.jpg);
    background-size: cover;
    padding-top: 20px;
    padding-bottom: 90px;
    height: 1200px;
}

.faq-section {
    padding: 80px 80px 0px 80px;
}

.main-faq-title {
  text-align: center;
  font-size: 3em;
  font-weight: bold;
  color: white;
}

.faq-container {
    position: center;
}

.faq-question {
    margin-top: 30px;
}

.content {
    width: 80%;
    padding: 20px;
    margin: 0 auto;
    padding: 0 60px 0 0;
  }
  
  .centerplease {
    margin: 0 auto;
    max-width: 270px;
    font-size: 40px;
  }
  
  .questions:checked ~ .answers{
    height: auto;
    opacity: 1;
    padding: 15px;
  }
  
  .plus {
    color:#fff;
    position: absolute;
    margin-left: 10px;
    margin-top: 5px;
    z-index: 5;
    font-size: 2em;
    line-height: 100%;
    -webkit-user-select: none;
    -ms-user-select: none;
    -o-user-select: none;
    user-select: none;
    transition: .3s ease;
  }
  
  .questions:checked ~ .plus {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
  }
  
  .questions {
    display: none;
  }

.faq-image {
    margin-top: 30px;
}

footer {
    margin-top: -30px;
    height: 500px;
    background-color: black;
    align-items: center;
    color: #fff;
}

.footer-fix {
    padding-top: 50px;
}

.options-footer {
    margin-bottom: 10px;
}

.text-footer {
    margin-top: 10px;
    text-align: center;
}

.fixed-footer {
    display: inline-block !important;
    margin: 0 auto;
}

.footer-options-new-li {
    background-color: #0A0A0A;
    margin: 5px;
    padding: 5px;
    border: 1px solid white;
    border-radius: 5px;
    width: 180px;
    transition: 0.3s;
}

.footer-options-new-a {
    color: white;
    font-weight: 900;
}

.footer-options-new-li:hover {
    background-color: #AFAFAF;
    margin: 5px;
    padding: 5px;
    border: 1px solid #0A0A0A;
    border-radius: 5px;
}

.footer-options-new-a:hover {
    color: #0A0A0A;
}

