.roadmap-initial {
    position: relative;
    width: 100%;
    height: 800px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
}

.roadmap-initial img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    pointer-events: none;
}

.board {
    width: 1200px !important;
}

.teacher {
    width: 1200px !important;
}

/* SECOND ANIMATION ROADMAP */

.roadmap-content {
    position: relative;
    width: 100%;
    height: 800px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url(/static/media/background-roadmap.dd99ab13.png);
}

.roadmap {
    position: absolute;
    width: 150px;
    height: 150px;
    left: 1.5rem;
    top: 5.5rem;
    opacity: 0;
}

.pizza {
    position: absolute;
    width: 150px;
    height: 150px;
    right: 1.5rem;
    top: 5.5rem;
    opacity: 0;
}

.roadmap1, .pizza1 {
    opacity: 1;
    z-index: 1;
}

.roadmap2, .pizza2 {
    opacity: 0;
    z-index: 2;
}

.roadmap3, .pizza3 {
    opacity: 0;
    z-index: 3;
}

.roadmap4, .pizza4 {
    opacity: 0;
