.main-team-title {
    text-align: center;
    font-size: 3em;
    font-weight: bold;
    color: white;
  }
  
  .team-box {
    border-radius: 10px;
  }
  
  .main-team-text {
      padding: 20px;
      font-size: 1.6em;
      font-weight: bold;
      color: white; 
  }
  
  .main-team-name {
      /*margin-top: 30px !important;*/
      text-align: center;
      padding: 20px;
      margin-left: 6% !important;
      font-size: 1.6em;
      font-weight: bold;
      color: white;
      text-shadow: #0A0A0A 1px 0 30px;
  }
  
  .main-team-role {
      padding: 2px;
      font-size: 1em;
      font-weight: 900;
      color: white;
      margin-left: 6% !important;
      text-align: center;
      padding-bottom: 40px;
      text-shadow: #0A0A0A 1px 0 30px;
  }
  /*
  a:link,
  a:active,
  a:visited{
    color:rgba(255,255,255,1);
  }
  
  a:hover{
    color:#2D9EEF;
  }
  */
  .team-member {
    margin: 15px 0;
    padding: 30px;
  }
  
  .team-member figure {
    position: relative;
    overflow: hidden;
    padding: 0;
    margin: 0;
  }
  
  .team-member figure img {
    min-width: 100%;
  }
  
  .team-member figcaption p {
    font-size: 16px;
  }
  
  .team-member figcaption ul {
    list-style: none;
    margin: 0;
    margin-left: 25%;
    padding: 0;
  }
  
  .team-member figcaption ul {
    visibility: visible;
    transition: all 0.1s ease-in-out;
  }
  
  .team-member figcaption ul li {
    display: inline-block;
    padding: 10px;
  }
  
  .team-member h4 {
    margin: 10px 0 0;
    padding: 0;
  }
  
  .team-member figcaption {
    padding: 50px;
    color: transparent;
    background-color: transparent;
    position: absolute;
    z-index: 996;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 0;
    overflow: hidden;
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }
  
  .team-member figure:hover figcaption {
    visibility: visible;
    color: #fff;
    background: rgba(0,127,14,0.3);
    background: var(--darkboxes);
    /* Primary color, can be changed via colors.css */
    
    height: 100%;
    transition: all 0.3s ease-in-out;
  }
  
  .team-member figure:hover figcaption ul li a:hover {
    color: #003504;
    color: var(--main-orange-color);
  }
  
  .team-member figure img {
    -webkit-transform: scale(1) rotate(0) translateY(0);
    transform: scale(1) rotate(0) translateY(0);
    transition: all 0.4s ease-in-out;
  }
  
  .team-member figure:hover img {
    -webkit-transform: scale(1.1) rotate(1deg) translateY(12px);
    transform: scale(1.1) rotate(1deg) translateY(12px);
    transition: all 0.4s ease-in-out;
  }

